import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-lorem-impsum',
  templateUrl: './lorem-impsum.component.html',
  styleUrls: ['./lorem-impsum.component.css']
})
export class LoremImpsumComponent implements OnInit {
UserName="Muhammad Yasir Bilal";
cBlue="clsBlue";
cRed="clsRed";
cGreen="clsGreen";
defaultValue="Yasir";
defaultPlaceholder="First Name";
times=0;
/*colors=[
  this.cBlue,this.cRed,this.cGreen
]*/
colors={
 clsGreen:true,
 clsBlue:false,
 clsRed:false
}
sObject={
  'color':'green',
  'font-family':'arial',
  'font-size':'30px'
}
show: boolean=false;//show is a property whose default value is false
fColor="";
fSize:number=30;
showView:number=0;
myArr=[
  'one',
  'two',
  'three',
  'four'
]
  constructor() { }

  ngOnInit() {
  }

  increaseClick(){
    this.times++;
  }
  changeBlock(){
    this.show=!this.show;
  }
  changeColor(val){
    this.fColor=val;
  }

}



